.header {
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position:fixed;
    width:100%;
    height:90px;
    z-index:10;
}

.header-bg {
    background-color: grey;
    transition:0.5s;
}

.nav-bar {
    display: flex;

}

.nav-bar:hover {
   color:aquamarine;
}

.nav-bar li {
    padding: 0 1rem;

}

.hamburger {
    display: initial;
}

@media screen and (max-width: 1040px) {
    .nav-bar{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height:100vh;
        background: rgba(0,0,0,0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.2s;
    }
    .nav-bar.active {
        left:0;
    }

    .nav-bar li {
        padding: 1rem 0;
    }

    .nav-bar li a {
        font-size: 2rem;
    }
}
.nav-bar a {
    font-size: 1.2rem;
    font-weight: 500;
    
}